.overlay-wrap{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 999;
    width: 100%;
    background-color: rgba(0,0,0,.8);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.popup-wrap{
    background-color: white;
    padding: 1.5rem;
    border-radius: 1rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    max-width: 90vw;

    &-header{
        display: flex;
        justify-content: space-between;
        align-items: center;

        &-title{
            font-weight: 600;
        }
        
        &-close{
            stroke: var(--color-persian-blue);
            height: 1.75rem;
            aspect-ratio: 1/1;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 10rem;
            background-color: var(--color-athens-grey);

            &:hover{
                background-color: var(--color-jumbo);
                stroke: var(--color-iron);
            }

            & svg {
                height: 40%;
            }
        }
    }

    &-content{
        display: flex;
        flex-wrap: wrap;
        gap: 1rem;

        & .language-item{
            border-radius: .5rem;
            display: flex;
            gap: 1rem;
            justify-content: center;
            align-items: center;
            border: none;
            color: var(--color-persian-blue);
            padding: 1.5rem;
            width: 100%;
            

            & svg {
                height: 2.5rem;
            }

            & h3{
                font-size: 1.25rem;
                font-weight: 600;
            }

            &:hover{
                background-color: var(--color-athens-grey);
                color: var(--color-persian-blue);
            }

            &.current{
                background-color: var(--color-athens-grey);
                color: var(--color-persian-blue);

            }
        }
    }
}
